/* Slider */
.slick-slider { position: relative; display: block; -moz-box-sizing: border-box; box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-arrow.slick-hidden { display: none; }

.slider__arrows, .slider-dark__arrows { position: absolute; right: 0; display: flex; align-items: center; }

.slider__arrows .slick-counter, .slider-dark__arrows .slick-counter { font-size: 18px; line-height: 22px; font-weight: 100; bottom: 0; right: 0; }

.slider__arrows .slick-counter .total::before, .slider-dark__arrows .slick-counter .total::before { content: "/"; font-size: 36px; line-height: 43px; margin-left: 6px; }

.slider__arrows .slick-counter .current, .slider-dark__arrows .slick-counter .current { font-size: 36px; line-height: 43px; }

.slider__next, .slider__prev, .slider-dark__next, .slider-dark__prev { padding: 0; width: 20px; height: 10px; transition: width 0.4s ease; -webkit-transition: width 0.4s ease; -moz-transition: width 0.4s ease; -ms-transition: width 0.4s ease; -o-transition: width 0.4s ease; }

.slider__next, .slider-dark__next { transform: rotate(180deg); -webkit-transform: rotate(180deg); -moz-transform: rotate(180deg); -ms-transform: rotate(180deg); -o-transform: rotate(180deg); margin-left: 16px; }

.slider__next:focus, .slider-dark__next:focus, .slider__next:hover, .slider-dark__next:hover { width: 54px; transform: rotate(0); -webkit-transform: rotate(0); -moz-transform: rotate(0); -ms-transform: rotate(0); -o-transform: rotate(0); }

.slider__prev, .slider-dark__prev { margin-right: 16px; }

.slider__prev:focus, .slider-dark__prev:focus, .slider__prev:hover, .slider-dark__prev:hover { width: 54px; transform: rotate(180deg); -webkit-transform: rotate(180deg); -moz-transform: rotate(180deg); -ms-transform: rotate(180deg); -o-transform: rotate(180deg); }

.slider__arrows { bottom: 66px; color: #fff; left: 0; }

.slider__arrows .slick-counter { color: #979797; }

.slider__arrows .slick-counter .current { color: #fff; }

.slider__next { background: url(../image/icons/arrow-left.svg) no-repeat center center; }

.slider__next:focus, .slider__next:hover { background: url(../image/icons/arrow-right.svg) no-repeat center center; }

.slider__prev { background: url(../image/icons/arrow-left.svg) no-repeat center center; }

.slider__prev:focus, .slider__prev:hover { background: url(../image/icons/arrow-right.svg) no-repeat center center; }

.slider-dark__arrows { top: -80px; justify-content: flex-end; color: #898989; width: 50%; }

.slider-dark__arrows .slick-counter { color: #898989; }

.slider-dark__arrows .slick-counter .current { color: #000; }

.slider-dark__next { background: url(../image/icons/arrow-left-dark.svg) no-repeat center center; }

.slider-dark__next:focus, .slider-dark__next:hover { background: url(../image/icons/arrow-right-dark.svg) no-repeat center center; }

.slider-dark__prev { background: url(../image/icons/arrow-left-dark.svg) no-repeat center center; }

.slider-dark__prev:focus, .slider-dark__prev:hover { background: url(../image/icons/arrow-right-dark.svg) no-repeat center center; }

@media only screen and (max-width: 767.8px) { .slider-dark__arrows { top: auto; bottom: -70px; width: 100%; justify-content: center; } }

