.footer__logo + p { font-weight: 100; line-height: 17px; }

.footer__middle { margin-top: 57px; }

.footer__middle .mt-20 { margin-top: 20px; }

.footer__nav { display: flex; justify-content: space-between; margin-top: 14px; }

.footer__menu li > a { color: #fff; }

.footer__menu li > a:hover { color: #FF9300; }

.footer__menu li > a:active, .footer__menu li > a.active { color: #FA4616; }

.footer__menu .counter { display: inline-block; font-weight: 300; font-size: 10px; line-height: 12px; padding: 1px 4px 2px; background-color: #ff1b1b; border-radius: 6.5px; -webkit-border-radius: 6.5px; -moz-border-radius: 6.5px; -ms-border-radius: 6.5px; -o-border-radius: 6.5px; margin-left: 4px; }

