.footer__line { width: 100%; opacity: 0.1; border-top: 1px solid #fff; }

.footer__right-col { text-align: right; }

.footer__right-col .who-created { color: #fff; font-weight: 100; margin-bottom: 8px; display: block; }

.call-us { color: #fff; margin-bottom: 94px; font-size: 18px; line-height: 22px; }

.call-us__meneger { font-weight: 100; text-align: center; }

.call-us__img { width: 159px; height: 159px; border-radius: 50%; -webkit-border-radius: 50%; -moz-border-radius: 50%; -ms-border-radius: 50%; -o-border-radius: 50%; overflow: hidden; display: flex; align-items: center; justify-content: center; margin-bottom: 20px; }

.call-us__img > img { width: 100%; height: 100%; object-fit: cover; }

.call-us__title { font-weight: 700; font-size: 36px; line-height: 43px; margin-bottom: 8px; }

