.call-us__info .phone .arrow-down > svg { width: 34px; height: 34px; }

.call-us__form { margin-top: 35px; display: flex; align-items: center; justify-content: space-between; }

.call-us__form .btn-red { min-width: 255px; margin-left: 30px; }

